import React, { useEffect, useState } from 'react';
import API from '../utils/API';

const SportsData = () => {
  const [sports, setSports] = useState([]);

  useEffect(() => {
    API.getSports()
      .then(res => {
        console.log(res.data);
        setSports(res.data);
      })
      .catch(err => {
          console.log(err);
      });
  }, []);

  return (
    <div>
      <ul>
        {sports.map(sport => (
          <li key={sport.key}>{sport.key}</li>
        ))}
      </ul>
    </div>
  );
};

export default SportsData;
